// router.js
import { createRouter, createWebHistory } from 'vue-router';
import ArticleOne from './components/ArticleOne.vue';

const routes = [
  {
    path: '/article/:id',
    name: 'article',
    component: ArticleOne,
    props: true
  }
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router;
