<template>
  <div class="article">
    <h3>{{ title }}</h3>
    <p>{{ content }}</p>
  </div>
</template>

<script>
import { ref } from 'vue';

export default {
  name: 'ArticleOne',
  setup() {
    const title = ref('記事1のタイトル');
    const content = ref('記事1の本文');

    return {
      title,
      content
    };
  }
}
</script>

<style scoped>
.article {
  margin-bottom: 20px;
}

.article h3 {
  font-size: 1.2em;
  margin-bottom: 10px;
}
</style>
